';'
import React from "react";
import ProfileCard from "@/app/components/dashborad/ProfileCard";
import StatusCard from "@/app/components/dashborad/StatusCard";
import AdCard from "@/app/components/ui/AdCard";
import homeIcon from "@/public/resourses/home.svg";
import allIcon from "@/public/resourses/all.svg";
import starIcon from "@/public/resourses/star.svg";
import Image from "next/image";

const ads = Array(8).fill({
  id: 1,
  offer_type: "sell",
  title: "عنوان البطاقة",
  description: "شقة حديثة للبيع في قلب دمشق - 3 غرف نوم، صالون واسع، مطبخ أمريكي",
  city: "حمص",
  price: 50000,
  imageUrl: undefined,
  published: "accepted",
  isFav: false,
  subcat: "apartments",
});

const page = () => {
  return (
    <div className="container mx-auto px-4 py-6" dir="rtl">
      {/* Profile Header */}
      <div className="flex flex-col md:flex-row items-center gap-6 mb-6">
        {/* Profile Card */}
        <div className="flex-1">
          <ProfileCard />
        </div>
        {/* Profile Details */}
        <div className="flex flex-col items-center justify-center">
          <span className="text-2xl font-bold">محمد فجلات</span>
          <div className="flex items-center gap-1 mt-2">
            <Image src={starIcon.src} alt="star" className="w-5 h-5" width={1} height={1} />
            <Image src={starIcon.src} alt="star" className="w-5 h-5" width={1} height={1} />
            <Image src={starIcon.src} alt="star" className="w-5 h-5" width={1} height={1} />
            <Image src={starIcon.src} alt="star" className="w-5 h-5" width={1} height={1} />
            <Image src={starIcon.src} alt="star" className="w-5 h-5" width={1} height={1} />
            <span className="text-cdarkgray text-sm mr-2">4.7 (35 تقييم)</span>
          </div>
          {/* Additional Profile Details */}
          <div className="mt-4">
            <p>حمص</p>
            <p>الخالدية</p>
            <p>963996435004+</p>
            <p>malekazon123@gmail.com</p>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex flex-col gap-6">
        {/* Tabs */}
        <div className="flex border-b border-cgreen mb-4">
          <button className="text-cgreen font-bold border-b-2 border-cgreen px-4 py-2">الإعلانات المنشورة</button>
          {/* Add more tabs if needed */}
        </div>

        {/* Ads Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {ads.map((ad, idx) => (
            <AdCard key={idx} {...ad} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default page;